import React, {useEffect, useCallback} from 'react'
import {Route, useRouteMatch} from 'react-router-dom'
import {useDispatch} from 'react-redux'
import BigNumber from 'bignumber.js'
import {useWallet} from '@binance-chain/bsc-use-wallet'
import {provider} from 'web3-core'
import {Image, Heading, Text} from '@pancakeswap-libs/uikit'
import {BLOCKS_PER_YEAR, CAKE_PER_BLOCK, CAKE_POOL_PID} from 'config'
import FlexLayout from 'components/layout/Flex'
import Page from 'components/layout/Page'
import {useFarms, usePriceBnbBusd, usePriceCakeBusd} from 'state/hooks'
import useRefresh from 'hooks/useRefresh'
import {fetchFarmUserDataAsync} from 'state/actions'
import {QuoteToken} from 'config/constants/types'
import useI18n from 'hooks/useI18n'
import FarmCard, {FarmWithStakedValue} from './components/FarmCard/FarmCard'
import FarmTabButtons from './components/FarmTabButtons'
import Divider from './components/Divider'
import TotalValueLocked from './components/TotalValueLocked'

const Farms: React.FC = () => {
    const {path} = useRouteMatch()
    const TranslateString = useI18n()
    const farmsLP = useFarms()
    const cakePrice = usePriceCakeBusd()
    const bnbPrice = usePriceBnbBusd()
    const {account, ethereum}: { account: string; ethereum: provider } = useWallet()

    const dispatch = useDispatch()
    const {fastRefresh} = useRefresh()
    useEffect(() => {
        if (account) {
            dispatch(fetchFarmUserDataAsync(account))
        }
    }, [account, dispatch, fastRefresh])

    const activeFarms = farmsLP.filter((farm) => farm.pid !== 0 && farm.multiplier !== '0X')
    const inactiveFarms = farmsLP.filter((farm) => farm.pid !== 0 && farm.multiplier === '0X')

    // /!\ This function will be removed soon
    // This function compute the APY for each farm and will be replaced when we have a reliable API
    // to retrieve assets prices against USD
    const farmsList = useCallback(
        (farmsToDisplay, removed: boolean) => {
            const cakePriceVsBNB = new BigNumber(farmsLP.find((farm) => farm.pid === CAKE_POOL_PID)?.tokenPriceVsQuote || 0)
            const farmsToDisplayWithAPY: FarmWithStakedValue[] = farmsToDisplay.map((farm) => {
                if (!farm.tokenAmount || !farm.lpTotalInQuoteToken || !farm.lpTotalInQuoteToken) {
                    return farm
                }
                const cakeRewardPerBlock = CAKE_PER_BLOCK.times(farm.poolWeight)
                const cakeRewardPerYear = cakeRewardPerBlock.times(BLOCKS_PER_YEAR)

                let apy = cakePriceVsBNB.times(cakeRewardPerYear).div(farm.lpTotalInQuoteToken)
                if (farm.quoteTokenSymbol === QuoteToken.BUSD || farm.quoteTokenSymbol === QuoteToken.UST) {
                    apy = cakePriceVsBNB.times(cakeRewardPerYear).div(farm.lpTotalInQuoteToken).times(bnbPrice)
                } else if (farm.quoteTokenSymbol === QuoteToken.CAKE) {
                    apy = cakeRewardPerYear.div(farm.lpTotalInQuoteToken)
                } else if (farm.dual) {
                    const cakeApy =
                        farm && cakePriceVsBNB.times(cakeRewardPerBlock).times(BLOCKS_PER_YEAR).div(farm.lpTotalInQuoteToken)
                    const dualApy =
                        farm.tokenPriceVsQuote &&
                        new BigNumber(farm.tokenPriceVsQuote)
                            .times(farm.dual.rewardPerBlock)
                            .times(BLOCKS_PER_YEAR)
                            .div(farm.lpTotalInQuoteToken)

                    apy = cakeApy && dualApy && cakeApy.plus(dualApy)
                }

                return {...farm, apy}
            })
            return farmsToDisplayWithAPY.map((farm) => (
                <FarmCard
                    key={farm.pid}
                    farm={farm}
                    removed={removed}
                    bnbPrice={bnbPrice}
                    cakePrice={cakePrice}
                    ethereum={ethereum}
                    account={account}
                />
            ))
        },
        [bnbPrice, farmsLP, account, cakePrice, ethereum],
    )

    return (
        <Page>

            <Heading as="h1" size="lg" color="primary" mb="20px" style={{textAlign: 'center'}}>
                {TranslateString(999, 'Stake APPLE-LP tokens to earn Apple')}
                <TotalValueLocked/>
            </Heading>
            <Text style={{textAlign: 'center'}} mb="20px" color="#e901e9">
                {TranslateString(9991, '🔥 FEES HARVEST: 10% TO BE BURNED 🔥')}
            </Text>
            <Text mt="16px" color="#e901e9" mb="50px" style={{textAlign: 'center'}}>
                {TranslateString(9991, '🎉 🍦 Apple Party! 4 rewards per block for the next 72 hours! ⏳')}
            </Text>


            <div>
                <FlexLayout>
                    <Route exact path={`${path}`}>
                        {farmsList(activeFarms, false)}
                    </Route>
                    <Route exact path={`${path}/history`}>
                        {farmsList(inactiveFarms, true)}
                    </Route>
                </FlexLayout>
            </div>
            <FarmTabButtons/>
        </Page>
    )
}

export default Farms
